@import url("https://fonts.googleapis.com/css2?family=Amiri:ital,wght@0,400;0,700;1,400&family=Roboto:wght@100;400;700&display=swap");

.Navbar {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

.logoContainer {
  margin: 28px 90px -15px 5px;
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: space-evenly;
  align-content: center;
}

.logoWrapperNav,
.menuWrapper,
.customerWrapper {
  display: flex;
}

#styleSettings {
  font-family: "Amiri", serif;
  font-size: 20px;
  font-weight: 400;
  color: #202020;
  margin: 0px 10px 0 10px;
}

.inputWrapper {
  width: 60%;
}

.inputField {
  font-family: inherit;
  width: 100%;
  height: 25px;
  border: 0;
  border-bottom: 2px solid grey;
  outline: 0;
  font-size: 1.3rem;
  color: black;
  padding: 6px 0;
  background: transparent;
}

.borderSolidLine {
  margin-top: 33px;
  border-bottom: solid grey;
  opacity: 0.6;
}

#logoText {
  font-family: "Amiri", serif;
  font-size: 32px;
  font-weight: 400;
  color: #202020;
  margin-left: 90px;
}

#logoText--span {
  font-family: "Amiri", serif;
  font-size: 32px;
  font-weight: 400;
  color: #c2872d;
}
#logoText--svg {
  margin-left: 10px;
}

/*dropdown */
.dropdown {
  background-color: white;
  display: flex;
  flex-direction: column;
  border: 1px solid gray;
}

.dropdown:empty {
  border: none;
}

.dropdown-row {
  cursor: pointer;
  text-align: start;
  margin: 2px 0;
}